import React from 'react';
import styled from 'styled-components';
import { graphql } from 'gatsby';
import Media from 'react-media';

import * as DISPLAYS from '../constants/displays';
import Layout from '../components/Layout';
import Horizontal from '../components/Horizontal';
import Link from '../components/Link';
import Button from '../components/Button';
import Image from '../components/Image';
import Categories from '../components/Categories';

// used in GraphQL query
const IMAGE_MAX_WIDTH = 720;
const IMAGE_MIN_WIDTH = 320;

const Container = styled.div`
  max-width: 1024px;
`;

const BlogList = styled(Horizontal)`
  display: flex;
  flex-direction: column;
`;

const BlogItem = styled(Horizontal)`
  margin: 40px 0;

  @media ${DISPLAYS.TABLET} {
    flex-direction: column;
    margin: 20px 0;
  }
`;

const BlogItemImage = styled.div`
  width: 100%;
  min-width: ${IMAGE_MIN_WIDTH}px;
  max-width: ${IMAGE_MAX_WIDTH}px;
`;

const BlogItemText = styled.div`
  margin: 0 20px;

  @media ${DISPLAYS.TABLET} {
    margin-top: 20px;
  }
`;

const Pagination = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`;

const Blog = ({
  data: { site, allMdx },
  pageContext: { pagination, categories, activeCategory },
}) => {
  const { page, nextPagePath, previousPagePath } = pagination;

  const posts = page.map(id =>
    allMdx.edges.find(edge => edge.node.id === id),
  );

  return (
    <Layout site={site} isCenter={true}>
      <Container>
        <BlogList>
          <Media query={DISPLAYS.MOBILE}>
            {matches =>
              matches ? null : (
                <Categories
                  categories={categories}
                  activeCategory={activeCategory}
                />
              )
            }
          </Media>
          {posts.map(({ node: post }) => (
            <BlogItem key={post.id}>
              {post.frontmatter.banner && (
                <BlogItemImage>
                  <Image
                    sizes={
                      post.frontmatter.banner.childImageSharp.sizes
                    }
                  />
                </BlogItemImage>
              )}

              <BlogItemText>
                <h2>
                  <Link to={post.frontmatter.slug}>
                    {post.frontmatter.title}
                  </Link>
                </h2>

                <small>{post.frontmatter.date}</small>

                <p>{post.excerpt}</p>

                <Button
                  aria-label="read more"
                  to={post.frontmatter.slug}
                >
                  Continue Reading
                </Button>
              </BlogItemText>
            </BlogItem>
          ))}
          <Pagination>
            <div>
              {nextPagePath && (
                <Button to={nextPagePath}>Next Page</Button>
              )}
            </div>

            <div>
              {previousPagePath && (
                <Button to={previousPagePath}>Previous Page</Button>
              )}
            </div>
          </Pagination>
        </BlogList>
      </Container>
    </Layout>
  );
};

export default Blog;

export const pageQuery = graphql`
  query {
    site {
      ...site
    }
    allMdx {
      edges {
        node {
          excerpt(pruneLength: 300)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            banner {
              childImageSharp {
                sizes(maxWidth: 720) {
                  ...GatsbyImageSharpSizes
                }
              }
            }
            slug
            categories
            keywords
          }
        }
      }
    }
  }
`;
