import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import Media from 'react-media';

import * as DISPLAYS from '../constants/displays';
import Vertical from '../components/Vertical';
import Link from '../components/Link';
import Button from '../components/Button';
import ReorderOption from '../icons/ReorderOption';

const landingNav = css``;

const nonLandingNav = css`
  background-color: #ffffff;
  box-shadow: 0px 1px 20px -10px rgba(0, 0, 0, 0.75);
  color: #a8a8a8;
`;

const Container = styled.nav`
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;

  ${({ isLanding }) => (isLanding ? landingNav : nonLandingNav)};
`;

const NavigationDefault = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
`;

const NavigationMobile = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-end;
  padding: 15px;
`;

const NavigationLink = styled(Link)`
  text-transform: uppercase;
  letter-spacing: 3.5px;
`;

const landingLink = css`
  color: #ffffff;
  opacity: 1;
  transition: opacity 0.25s ease-in-out;

  &:hover {
    color: #ffffff;
    opacity: 0.35;
  }

  &:visited {
    color: #ffffff;
  }
`;

const NavigationLinkDefault = styled(NavigationLink)`
  margin: 0 20px;

  ${({ isLanding }) => isLanding && landingLink};
`;

const NavigationLinkMobile = styled(NavigationLink)`
  margin: 5px 0;

  ${({ isLanding }) => isLanding && landingLink};
`;

const landingButton = css`
  &:hover {
    color: #ffffff;
    background-color: #7d7d7e;
    border-color: #7d7d7e;

    svg,
    path {
      fill: #ffffff;
      stroke: #ffffff;
    }
  }
`;

const MenuButton = styled(Button)`
  ${({ isLanding }) => isLanding && landingButton};
`;

class Navigation extends Component {
  constructor(props) {
    super(props);

    this.state = {
      windowHeight: 0,
      isLanding: true,
    };

    this.onScroll = this.onScroll.bind(this);
    this.onResize = this.onResize.bind(this);
  }

  componentDidMount() {
    this.setState(() => ({ windowHeight: window.innerHeight }));

    window.addEventListener('scroll', this.onScroll);
    window.addEventListener('resize', this.onResize);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
    window.removeEventListener('resize', this.onResize);
  }

  onScroll(event) {
    const { windowHeight } = this.state;
    const { navigation, isOpen } = this.props;

    const navigationHeight = isOpen ? navigation.length * 45 : 55;

    if (window.scrollY + navigationHeight < windowHeight) {
      this.setState(() => ({ isLanding: true }));
    } else {
      this.setState(() => ({ isLanding: false }));
    }
  }

  onResize(event) {
    this.setState(() => ({
      windowHeight: window.innerHeight,
    }));
  }

  render() {
    const { navigation, isHero, isOpen, toggleOpen } = this.props;
    const { isLanding } = this.state;

    return (
      <Container isLanding={isLanding && isHero}>
        <Media query={DISPLAYS.MOBILE}>
          {matches =>
            matches ? (
              <NavigationMobile>
                {isOpen && (
                  <Vertical>
                    {navigation.map(place => (
                      <NavigationLinkMobile
                        isLanding={isLanding && isHero}
                        key={place.to}
                        to={place.to}
                      >
                        {place.label}
                      </NavigationLinkMobile>
                    ))}
                  </Vertical>
                )}
                <div>
                  <MenuButton
                    isLanding={isLanding && isHero}
                    aria-label="toggle navigation"
                    onClick={toggleOpen}
                  >
                    <ReorderOption height="24px" width="24px" />
                  </MenuButton>
                </div>
              </NavigationMobile>
            ) : (
              <NavigationDefault>
                {navigation.map(place => (
                  <NavigationLinkDefault
                    isLanding={isLanding && isHero}
                    key={place.to}
                    to={place.to}
                  >
                    {place.label}
                  </NavigationLinkDefault>
                ))}
              </NavigationDefault>
            )
          }
        </Media>
      </Container>
    );
  }
}

export default Navigation;
